<template>
  <div class="app-bar">
    <div class="app-bar__content">
      <span class="app-bar__project-row">
        <svg v-on:click="goBack" class="logo-icon link u-spacing-mr-l" width="150" height="50">
          <use xlink:href="/static/assets/sprites.svg#logo"></use>
        </svg>
        <a href="#" v-on:click.prevent="goToProject('/projects/new')" class="u-spacing-mr-l">Start a Project</a>
      </span>

      <span>

        <button-basic v-if="!user.uid" 
          label="Login with Facebook" 
          modifiers="button--primary"
          v-bind:class="{'is--loading': isLoading}" 
          v-on:action="loginUser"></button-basic>
        <!-- <div v-if="user.uid" class="profile-image img-container" v-on:click="logOut">
          <img v-bind:src="user.photoURL" alt="">
        </div> -->
        <span v-if="user.uid" v-on:click="logOut">{{user.displayName}}</span>
       

      </span>
      
    </div>
  </div>
</template>


<script>

import firebase from 'firebase'
import firebaseui from 'firebaseui'
import ButtonBasic from '@/components/ButtonBasic'

export default {
  name: 'app-bar',
  created: function(){
    // console.log("app bar created...", this.user)
  },
  methods: {
    goBack: function(){
        console.log("user", this.user.uid)

        if (!this.user.uid) {
          this.$router.replace("/");
        } else {
          this.$router.replace("/projects");
        }
        
    },
    goToProject: function(path){
      this.$router.push({ path: path })
    },
    loginUser: function(){

      console.log(this);
      var _this = this;

      _this.isLoading = true


      var provider = new firebase.auth.FacebookAuthProvider();
      

      firebase.auth().signInWithPopup(provider).then(function(result) {
        // This gives you a Facebook Access Token. You can use it to access the Facebook API.

        console.log(this);
        var token = result.credential.accessToken;
        // The signed-in user info.
        var user = result.user;

        _this.isLoggedIn = true;

        _this.isLoading = false;
        
        // console.log(user);

        // ...
      }).catch(function(error) {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
    },
    logOut: function(){
      var _this = this;

      this.isLoggedIn = false
      firebase.auth().signOut().then(function() {
        // Sign-out successful.
        // alert ("signed out");
        _this.$router.replace("/")
        this.$emit("loggedOut");
        

      }).catch(function(error) {
        // An error happened.
      });
    }
  },
  components: {
    ButtonBasic
  },
  props: {
    user: {}
  },
  data () {
    return {
      isLoggedIn: false,
      isLoading: false
    }
  }
}
</script>

<style lang="scss">

.logo-icon {
  cursor: pointer;
}



  .app-bar {
    width: 100%;
  }

  .app-bar__content {
    height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 3vw 0 1vw;
    background: white;
    border-bottom: 1px #f3f3f3 solid;
  }

  .u-spacing-mr-m {
    margin-right: 8px 
  }

  .u-spacing-mr-l {
    margin-right: 32px 
  }

  .app-bar__project-row {
    display: flex;
    align-items: center;

    a {
      font-weight: 500;
      font-size: 18px;
    }

  }

  .profile-image {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }



</style>


